.btn{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    border-radius: 100px;
    background: var(--orange-color);
    color: var(--white-color);
    border: 0;
    cursor: pointer;
}

.btn.disabled{
    background: var(--grey1-color);
    color: var(--white-color);
    cursor: default;
}

.btn__exit{
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 50px;
	border-radius: 100px;
	background: transparent;
	color: var(--grey3-color);
	border: 0;
	cursor: pointer;
}