.add textarea.input{
    height: 111px;
    margin: 24px 0 20px 0;
}

.add .block-input textarea.input:focus~.label,
.add .block-input textarea.input:not(:placeholder-shown)~.label {
    transform: translateY(-6px);
}

.add textarea.input ~ .label {
    top: -35px;
}

.add__form-cancel{
    display: block;
    margin-top: 16px;
    text-align: center;
    color: var(--grey3-color)
}